.footer {
  color: #ffffff;
}
.footer .footer_signup {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
  background-color: #171819;
}
.footer .footer_signup .footer_signup-title {
  margin-right: 70px;
  min-width: 205px;
  font-size: 22px;
  line-height: 26px;
  text-align: center;
  letter-spacing: 0.846154px;
}
.footer .footer_signup .footer_signup-form form {
  display: flex;
}
.footer .footer_signup .footer_signup-form form input {
  width: 250px;
  padding: 10px 16px;
  font-size: 14px;
  line-height: 16px;
  border: 1px solid #979797;
}
.footer .footer_signup .footer_signup-form form .form-btn {
  margin: 0;
}
.footer .footer_contact-info {
  background-color: #141A20;
}
.footer .footer_contact-info .container {
  display: flex;
  align-items: center;
  height: 240px;
  gap: 30px;
}
.footer .footer_contact-info .container .contact-info_logo {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  width: 37%;
  height: 150px;
}
.footer .footer_contact-info .container .contact-info_logo .logo_company {
  font-size: 11px;
  line-height: 13px;
  text-align: center;
  letter-spacing: 0.846154px;
}
.footer .footer_contact-info .container .contact-info_logo .logo_link {
  margin-left: -5px;
}
.footer .footer_contact-info .container .contact-info_logo .logo_text {
  font-size: 11px;
  line-height: 19px;
  letter-spacing: 0.85px;
}
.footer .footer_contact-info .container .contact-info_links {
  width: 40%;
}
.footer .footer_contact-info .container .contact-info_links p {
  margin-bottom: 15px;
  font-size: 11px;
  line-height: 13px;
  letter-spacing: 0.846154px;
}
.footer .footer_contact-info .container .contact-info_links .links-list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 125px;
}
.footer .footer_contact-info .container .contact-info_links .links-list .links-list_item {
  width: fit-content;
  font-size: 11px;
  line-height: 25px;
  letter-spacing: 0.846154px;
}
.footer .footer_contact-info .container .contact-info_contacts {
  width: 23%;
}
.footer .footer_contact-info .container .contact-info_contacts p {
  margin-bottom: 15px;
  font-size: 11px;
  line-height: 13px;
  letter-spacing: 0.846154px;
}
.footer .footer_contact-info .container .contact-info_contacts .contacts_address p,
.footer .footer_contact-info .container .contact-info_contacts .contacts_address a {
  font-size: 11px;
  line-height: 16px;
  letter-spacing: 0.846154px;
}
.footer .footer_contact-info .container .contact-info_contacts .contacts_address a {
  display: block;
  margin-bottom: 15px;
}
.footer .footer_contact-info .container .contact-info_social {
  display: flex;
  align-self: flex-start;
  gap: 25px;
  padding-top: 60px;
}
.footer .footer_contact-info .container .contact-info_social .social {
  width: 12px;
  height: 12px;
  filter: invert(1);
}
.footer .footer_agreement {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
  font-size: 11px;
  line-height: 13px;
  letter-spacing: 0.846154px;
  background-color: #171819;
}